import DashboardLayout from '../pages/Layout/DashboardLayout'
const Dashboard = () =>
  import(/* webpackChunkName: "dashboard" */ '../pages/Dashboard/index')

// Merchant menu
const mRegistered = () =>
  import('../pages/Merchant/registered')

const mRegistration = () =>
  import('../pages/Merchant/registration')

const mSamaRequests = () =>
  import('../pages/Products/SAMA/Admin/requests')

const mSamaSettings = () =>
  import('../pages/Products/SAMA/Admin/settings')

const mSamaPrograms = () =>
  import('../pages/Products/SAMA/Programs/programs')

const mSamaCampaigns = () =>
  import('../pages/Products/SAMA/Campaigns/campaigns')

const mSamaUsers = () =>
  import('../pages/Products/SAMA/Users/users')

const mSamaTransaction = () =>
  import('../pages/Products/SAMA/Transaction/transaction')

let merchantMenu = {
  path: '/merchants',
  component: DashboardLayout,
  redirect: '/merchants/registered-merchant',
  name: 'Merchants',
  children: [
    {
      path: 'registered-merchant',
      name: 'Registered',
      components: {default: mRegistered}
    },
    {
      path: 'merchant-registration',
      name: 'add new',
      components: {default: mRegistration}
    }
  ]
}

let productsMenu = {
  path: '/SAMA',
  component: DashboardLayout,
  redirect: '/SAMA/Admin/requested',
  name: 'Merchants',
  children: [
    {
      path: '/SAMA/Admin/requested',
      name: 'Requests',
      components: {default: mSamaRequests}
    },
    {
      path: '/SAMA/Admin/sama-settings',
      name: 'Settings',
      components: {default: mSamaSettings}
    },
    {
      path: '/SAMA/Programs/programs',
      name: 'Programs',
      components: {default: mSamaPrograms}
    },
    {
      path: '/SAMA/Campaigns/campaigns',
      name: 'Campaigns',
      components: {default: mSamaCampaigns}
    },
    {
      path: '/SAMA/Users/users',
      name: 'Users',
      components: {default: mSamaUsers}
    },
    {
      path: '/SAMA/Transaction/transaction',
      name: 'Transactions',
      components: {default: mSamaTransaction}
    }
  ]
}

const routes = [
  {
    path: '/',
    redirect: '/dashboard',
    name: 'Home'
  },
  merchantMenu,
  productsMenu,
  {
    path: '/',
    component: DashboardLayout,
    redirect: '/dashboard',
    name: 'Dashboard layout',
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        components: { default: Dashboard }
      }
    ]
  },
  {
    path: '*',
    redirect: '/dashboard'
  }
  
]

export default routes
